body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    background: #355ECB;
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}
.box {
    padding: 30px 50px;
    border: 1px solid rgba(121, 120, 120,.4);
    border-radius: 30px;
    box-shadow: 2px 2px 10px rgba(145, 144, 144, 0.1);
    max-width: 700px;
    background: #fff;
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
header span {
    color: #355ECB;
    border-radius: 5px;
}
header h3 {
    position: relative;
}
header h3:before {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #355ECB;
    right: -30px;
    top: -1px;
}
.filter {
    padding: 5px;
    border: 1px solid #A7B1CB;
    border-radius: 20px;
    width: 50px;
    height: 20px;
    background: transparent;
    position: relative;
    cursor: pointer;
}
.filter span {
    border-radius: 20px;
    width: 20px;
    height: 17px;
    padding: 2px 16px;
    background: #4873E6;
    position: relative;
    top: 0px;
    left: -2px;
    transition: 0.5s;
}
.filter span::before {
    content: "";
    width: 2px;
    height: 18px;
    background: #fdfdfd;
    position: absolute;
    top: 3px;
    left: 45%;
}
section {
    display:flex;
    align-items:flex-end;
    justify-content: space-between;
    padding: 20px 20px 20px 0;
}
section img {
    width: 250px;
    height: 250px;
    margin-left: 0;
    margin-right: 20px;
    animation: fade 1s ease-in;
}
section a img {
    width: 15px;
    height: 15px;
    margin: auto;
}
section .para {
    width: 70%;
    min-width: 200px;
}
section #text {
    font-size: 25px;
    padding: 20px 0px;
    text-align: center;
    max-width: 450px;
    color: #6F7EE6;
    animation: fade 1s ease-in;
}
section #author {
    text-align: center;
    padding-bottom: 20px;
    font-style: italic;
}
.social {
    width: 100%;
    text-align: center;
}
.social a {
    text-decoration: none;
    color: #355ECB;
    padding: 5px 10px;
    padding-top: 10px;
    text-align: center;
    margin: 5px;
    border: 1px solid rgb(224, 224, 224);
    border-radius: 50%;
    background: rgb(255, 255, 255);
    box-shadow: 1px 2px 5px rgba(136, 136, 136, .33);
}
button#new-quote {
    width: 100%;
    height: 50px;
    background: #355ECB;
    color: #fff;
    border: none;
    margin-top: 40px;
    padding: 0 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 12px;
}
button#new-quote:hover {
    background: #244296;
}

.dark.container {
    color: #fff;
}
.dark .box {
    background: #191929;
}

@media screen and (max-width: 500px) {
    .box {
        width: 95%;
        margin: auto;
    }
}

@keyframes fade {
    from { opacity: 0.5;}
    to {opacity: 1}
}